<script>
// @ is an alias to /src
import WOW from '@/plugins/wow.min.js';
import SiteServices from '@/services/SiteServices';
import ResenaService from '@/services/ResenasService';
import { Carousel3d, Slide } from 'vue-carousel-3d';
// var wow = new WOW({ scrollContainer: "#scrolling-body"});
export default {
  name: 'Home',
  data(){
    return{
		image_user:'',
		valid: true,
		camposRules: [
			v => !!v || 'El campo es requerido'
		],
		contenidoRules: [v => v.length <= 600 || 'Max. 600 caracteres'],
		list_resenas:[
		],
		dialog_resena :false,
		resena:{
			autor:'',
			titulo:'',
			contenido:'',
			fecha_publicacion:new Date().toISOString(),
			rating:0, 
			estado:0, 
			img_user:''
		},
		tab_atractivos:null,
      	bg:'transparent',
      	altura_ini_p:500,
	  	drawer:false,
      	icons: [
			'mdi-facebook',
			'mdi-twitter',
			'mdi-linkedin',
			'mdi-instagram',
        ],
      	items: [
			{
				src: 'https://www.incaworldbolivia.com/fotos/0915201694507-Isla-del-sol-Bolivia.jpg',
			},
			{
				src: '../assets/copaini.png',
			},
		],
		list_atractivos:[],
		list_eventos:[],
		list_actividades:[],
		list_noticias:[],
		list_hospedaje:[]
    }
  },
  mounted(){	
		// this.altura_ini_p=(window.innerHeight)-64;
		this.altura_ini_p=(window.innerHeight);
    	window.onscroll = () => {
			this.changeColor();
		};
		setTimeout(() => (this.activa_inicio()), 1000);
		this.scroll_ini();
		this.get_atractivos();
		this.get_eventos();
		this.get_hospedajes();
		this.get_noticias();
		this.get_resenas();	
		this.get_actividades();
  },
  filters:{
	fecha_literal:function(value){
		let fecha_literal ='';
		if (value){
		let fecha = new Date(value);
		let dia = ('0'+(fecha.getDate())).slice(-2);
    	let anio = fecha.getFullYear();
		let mes = fecha.getMonth()+1;
		let mesLiteral = '';
		switch(mes){
			case 1:
				mesLiteral = 'Enero';
				break;
			case 2:
				mesLiteral = 'Febrero';
				break;
			case 3:
				mesLiteral = 'Marzo';
				break;
			case 4:
				mesLiteral = 'Abril';
				break;
			case 5:
				mesLiteral = 'Mayo';
				break;
			case 6:
				mesLiteral = 'Junio';
				break;
			case 7:
				mesLiteral = 'Julio';
				break;
			case 8:
				mesLiteral = 'Agosto';
				break;
			case 9:
				mesLiteral = 'Septiembre';
				break;
			case 10:
				mesLiteral = 'Octubre';
				break;
			case 11:
				mesLiteral = 'Noviembre';
				break;
			case 12:
				mesLiteral = 'Diciembre';
				break;
			} 
			fecha_literal = `${dia} de ${mesLiteral} de ${anio}`;
		}
		return fecha_literal;
	},
	fecha_literal_mes:function(value){
		let fecha_literal ='';
		if (value){
		let fecha = new Date(value);
		let dia = ('0'+(fecha.getDate()+1)).slice(-2);
		let mes = fecha.getMonth()+1;
		let mesLiteral = '';
		switch(mes){
			case 1:
				mesLiteral = 'Enero';
				break;
			case 2:
				mesLiteral = 'Febrero';
				break;
			case 3:
				mesLiteral = 'Marzo';
				break;
			case 4:
				mesLiteral = 'Abril';
				break;
			case 5:
				mesLiteral = 'Mayo';
				break;
			case 6:
				mesLiteral = 'Junio';
				break;
			case 7:
				mesLiteral = 'Julio';
				break;
			case 8:
				mesLiteral = 'Agosto';
				break;
			case 9:
				mesLiteral = 'Septiembre';
				break;
			case 10:
				mesLiteral = 'Octubre';
				break;
			case 11:
				mesLiteral = 'Noviembre';
				break;
			case 12:
				mesLiteral = 'Diciembre';
				break;
			} 
			fecha_literal = `${dia} de ${mesLiteral}`;
		}
		return fecha_literal;
	},
  },
  methods:{
	openLink(module, id){
		this.$router.replace('/'+module+'/'+id);
	},
	limpiarResena(){
		this.resena = {
			autor:'',
			titulo:'',
			contenido:'',
			fecha_publicacion:new Date().toISOString(),
			rating:5, 
			estado:0, 
			img_user:''
		};
		this.image_user='';
	},
	toBase64(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = error => reject(error);
      });
    },

	updateFile(event) {
      if(event!=null){
        // generamos un nuevo nombre de imagen
        var fileName = event.name;
        var extFile = fileName.split('.').pop();
        this.resena.img_user=(Math.random().toString(16).slice(2)) +'.'+ extFile;
        // para la visualizacion convertimos la imagen
        const file = event; 
        this.toBase64(file).then(base64 => {
          this.image_user=base64;
        });
      }else{
        this.image_user='';
      }
    },
	validateResena(){
		if(this.$refs.form.validate()){
			this.guardaResena()
		}
	},
	 FormDataImage(id_element, nombre_archivo){
      const fileinput= document.getElementById(id_element);
      if(fileinput.files.length!=0){
        const formData = new FormData();
        formData.append('file', fileinput.files[0], nombre_archivo);
        return formData;
      }else{
        return null;
      }
    },
	guardaResena(){
		var dataimagen=this.FormDataImage('file_imagen_principal', this.resena.img_user);
      	//this.resena.img_user="";

		//this.resena.fecha_publicacion = new Date().toISOString();
		ResenaService.saveResena(this.resena)
		.then(response=>{
			let id_resena = response.data.id;
			if(dataimagen!=null) {
				this.guardaImagenUser(id_resena, dataimagen);
				}
			this.$swal.fire(
			'Buen trabajo!',
			'Tu resea ha sido registrada y esta a la espera de su aprobacin, agradecemos tu gentil colaboracin',
			'success'
			);
			this.limpiarResena();
			this.dialog_resena = false;
		})
	},
	guardaImagenUser(id_resena, dataimagen){
      ResenaService.saveImage(id_resena, dataimagen).then(response=>{
        console.log(response.data);
        this.image_user='';
      })
    },
	scroll_ini(){
		document.querySelector('#scrolling-body').scrollTo(0,0);
	},
	get_actividades(){
		SiteServices.getActividad(5).then(response=>{
			this.list_actividades=response.data;
		})
	},
	get_eventos(){
		SiteServices.getEventosLimit(8).then(response=>{
			this.list_eventos=response.data;

			this.list_eventos.forEach(element=>{
				if(element.fecha!=null){
					let fecha = new Date(element.fecha);
					element.fecha = this.fecha_literal(fecha);
				}
			})
		})
	},
	get_atractivos(){
		SiteServices.getAtractivoLimit(8).then(response=>{
			this.list_atractivos=response.data;
		})
	},
	get_hospedajes(){
		SiteServices.getDataLimit('hospedajes', 15).then(response=>{
			this.list_hospedaje=response.data;
		})
	},
	get_noticias(){
		SiteServices.getDataLimit('noticias', 4).then(response=>{
			this.list_noticias=response.data;
		})
	},
	get_resenas(){
		SiteServices.getResenaLimit(4).then(response=>{
			this.list_resenas=response.data;
			// this.list_resenas.forEach(element=>{
			// var fecha = new Date(element.fecha_publicacion);
			// element.fecha_publicacion = this.ordenaFecha(fecha);
			// })
		})
	},
	ordenaFecha(fecha){
      let dia = ('0'+fecha.getDate()).slice(-2);
      let mes = ('0'+(fecha.getMonth()+1)).slice(-2);
      let anio = fecha.getFullYear();
      return `${dia}-${mes}-${anio}`;
     },
    activa_inicio(){
      var wow = new WOW({ scrollContainer: "#scrolling-body"});
      wow.init();
    },
    changeColor() {
      if (
          document.body.scrollTop > 100 ||
          document.documentElement.scrollTop > 100
      ) {
          this.bg = 'white';
      } else {
          this.bg = 'transparent';
      }
    },
  },
  components: {
	'carousel-3d': Carousel3d,
    'slide':Slide
  }
}
</script>
